<template>
    <div class="component authenticate">
        <div class="container text-center">
            <div>
                <string-blocks class="title-block" v-bind:string="'SIGN UP'"></string-blocks>
            </div>
            <hr/>
            <div class="row">
                <div class="col-12">
                    <authenticate-form v-bind:disabled="isLoading" v-on:submit="onSubmit" v-bind:register="true"></authenticate-form>
                    {{ error }}
                </div>
            </div>

            <template v-if="isLoading">
                <div class="row">
                    <div class="col-12">
                        <div class="loading-container">
                            <loading-spinner class="loading-spinner" color="#ffffff"></loading-spinner>
                        </div>
                    </div>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
    import LoadingSpinner from 'vue-spinner/src/MoonLoader'
    import AuthenticateForm from '../components/authenticate-form'
    import StringBlocks from '../components/string-blocks'
    import Block from '@/components/block'
    import UserService from '@/services/user'

    export default {
        name: 'authenticate',
        data() {
            return {
                error: null,
                isLoading: false
            }
        },
        props: {},
        watch: {},
        created() {

        },
        mounted() {
        },
        methods: {
            onSubmit(event) {
                this.isLoading = true;
                this.error = null;
                let email = event.email;
                let password = event.password;
                this.register(email, password)
                    .finally(() => {
                        this.isLoading = false;
                    })
            },
            register(email, password) {
                return UserService
                    .create(email, password)
                    .then(() => {
                        return this.authenticate(email, password);
                    })
                    .catch((err) => {
                        this.error = err.response.data.error;
                    });
            },
            authenticate(email, password) {
                return UserService.authenticate(email, password)
                    .then((token) => {
                        this.$router.push({
                            name: 'start'
                        });
                    })
                    .catch((err) => {
                        this.error = err.response.data.error;
                    });
            }
        },
        computed: {
            token() {
                return UserService.getToken();
            }
        },
        components: {
            LoadingSpinner,
            Block,
            StringBlocks,
            AuthenticateForm
        },
        mixins: []
    }
</script>

