import LocalStorageKeys from '@/constants/LocalStorageKeys'
import axios from 'axios'
import store from 'store'
import config from '@/config'
import jwtDecode from 'jwt-decode'

export default {
    getUserFromToken() {
        let token = this.getToken();

        if (!token) {
            return;
        }

        return jwtDecode(token);
    },
    create(email, password) {
        console.log(email, password);
        return axios.post(config.api.path + '/user', {
            email,
            password
        })
    },
    authenticate(email, password) {
        return axios.post(config.api.path + '/token', {
            email,
            password
        })
            .then((res) => {
                let token = res.data.data;
                this.setToken(token);
                return token;
            })
    },
    logout() {
        store.remove(LocalStorageKeys.TOKEN);
    },
    setToken(token) {
        store.set(LocalStorageKeys.TOKEN, token);
    },
    getToken() {
        return store.get(LocalStorageKeys.TOKEN);
    }
}