<template>
    <div class="component text-left">
        <form v-on:submit.prevent="onSubmit">
            <fieldset v-bind:disabled="disabled">
                <div class="row">
                    <div class="col-12">
                        <label>Email Address</label>
                        <input v-model="email" class="form-control" type="email" placeholder="Email Address">
                    </div>
                    <div class="col-12">
                        <label>Password</label>
                        <input v-model="password" class="form-control" type="password" placeholder="Password">
                    </div>
                </div>

                <hr/>

                <div class="row">
                    <div class="col-6">
                        <router-link v-if="register" class="btn-dark btn" v-bind:to="{name:'authenticate'}">Login
                        </router-link>
                        <router-link v-else="" class="btn-dark btn" v-bind:to="{name:'register'}">Sign Up</router-link>
                    </div>
                    <div class="col-6 text-right">
                        <button class="btn-orange btn" type="submit">
                            <template v-if="register">
                                Register
                            </template>
                            <template v-else="">
                                Log in
                            </template>
                        </button>
                    </div>
                </div>
            </fieldset>
        </form>
    </div>

</template>

<script>
    export default {
        name: 'Authenticate',
        data() {
            return {
                email: null,
                password: null
            }
        },
        props: {
            register: {
                type: Boolean,
                default() {
                    return false;
                }
            },
            disabled: {
                type: Boolean,
                default() {
                    return false;
                }
            }
        },
        watch: {},
        methods: {
            onSubmit() {
                this.$emit('submit', {
                    email: this.email,
                    password: this.password
                })
            }
        },
        computed: {},
        components: {}
    }
</script>

