import axios from 'axios'
import config from '@/config'
import UserService from '@/services/user'

export default {
    create(data) {
        return axios.post(config.api.path + '/entry', data, {
            headers: {
                'Authorization': 'Bearer ' + UserService.getToken()
            }
        });
    },
    top(hash) {
        return axios.get(config.api.path + '/entry/track/' + hash + '/top');
    }
}