<template>
    <div class="component landing">
        <div class="container">
            <h1>Landing</h1>
            {{ hasToken }}
        </div>
    </div>
</template>

<script>
    import _ from 'lodash'
    import UserService from '@/services/user'

    export default {
        name: 'landing',
        data() {
            return {}
        },
        props: {},
        watch: {},
        created() {

        },
        mounted() {
            if (this.hasToken) {
                this.$router.push({
                    name: 'start'
                })
            } else {
                this.$router.push({
                    name: 'authenticate'
                })
            }
        },
        methods: {},
        computed: {
            hasToken() {
                return !_.isEmpty(UserService.getToken());
            }
        },
        components: {},
        mixins: []
    }
</script>

