<template>
    <div class="component">
        <template v-if="isLoading">
            <div class="loading-container">
                <loading-spinner style="display: inline-block" color="#ffffff"></loading-spinner>
            </div>
        </template>
        <template v-else="">
            <div class="container">
                <div class="row" v-for="(entry, index) in top">
                    <div class="col-3">
                        <block class="rank" v-bind:value="(index + 1)"></block>
                    </div>
                    <div class="col-9">
                        <span class="email">{{ entry.user_email }}</span>
                        <span class="total-solutions">{{ entry.total_solutions }} solutions</span>
                        <span class="time-average">{{ (entry.time_average / 1000).toFixed(2) }} seconds per solution</span>
                    </div>
                    <div class="col-12">
                        <hr/>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
    import LoadingSpinner from 'vue-spinner/src/MoonLoader'
    import EntryService from '../../services/entry'
    import Block from '../block'

    export default {
        name: 'Number',
        data() {
            return {
                isLoading: false,
                top: []
            }
        },
        props: {},
        watch: {},
        methods: {
            retrieve() {
                this.isLoading = true;

                EntryService.top(this.$route.params.hash)
                    .then((result) => {
                        this.top = result.data.data;
                    })
                    .finally(() => {
                        this.isLoading = false;
                    })
            }
        },
        computed: {},
        mounted() {
            this.retrieve();
        },
        components: {
            Block,
            LoadingSpinner
        }
    }
</script>

