    .rank {
        font-size: 6px;
    }

    .rank,
    .email,
    .time-average,
    .total-solutions {
        text-align: left;
        display: block;
        color: white;
        text-shadow: 1px 1px 1px #000;
    }

    .loading-container {
        text-align: center;
        padding-top: 1em;
    }
</style>
